*{margin:0 auto;padding:0;box-sizing:border-box;color:black;font-family:"Muli", sans-serif}.chat{width:400px;height:600px;background-color:#ECF0F1;padding:1rem;display:flex;flex-direction:column;justify-content:space-between}.chat .user-name{text-align:start;width:100%}.chat .user-name h2{font-weight:300;border-bottom:1px solid rgba(255,255,255,0.1);padding-bottom:1rem}.chat .chat-message{height:70%;overflow-y:auto;display:flex;flex-direction:column;width:100%;align-content:flex-start}.chat .chat-message::-webkit-scrollbar{width:5px;height:5px}.chat .chat-message::-webkit-scrollbar-thumb{background:#fff;border-radius:10px}.chat .chat-message::-webkit-scrollbar-track{background:#ffac41;border-radius:10px}.chat .chat-message{scrollbar-face-color:#fff;scrollbar-track-color:#ffac41}.chat .chat-message .message{margin-left:0px;max-width:220px;padding-left:0.5rem}.chat .chat-message .message p{font-size:1rem;background-color:#343841;padding:1rem;border-radius:0px 10px 10px 10px;font-weight:300;color:white}.chat .chat-message .message span{font-size:0.7rem;font-weight:500;color:#343841;padding-left:0.5rem}.chat .chat-message .mess-right{margin-left:auto;margin-right:0px;display:flex;flex-direction:column;max-width:220px;padding-right:0.5rem}.chat .chat-message .mess-right p{text-align:end;border-radius:10px 0px 10px 10px;background-color:#ff1e56;color:white}.chat .chat-message .mess-right span{width:100%;text-align:end;padding-left:0rem;padding-right:0.5rem}.chat .send{width:100%;height:50px;display:flex}.chat .send input{width:80%;text-decoration:none;background-color:#A9CCE3;color:darkslategray;border:none;padding-left:1rem;border-radius:5px 0px 0px 5px}.chat .send input:focus{outline:none}.chat .send button{width:20%;border:none;background-color:#ffac41;border-radius:0px 5px 5px 0px}.chat .send button:hover{cursor:pointer}

*{margin:0 auto;padding:0;box-sizing:border-box;color:black;font-family:"Muli", sans-serif}.process{width:500px;min-height:550px;margin-right:10rem;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;padding:2rem}.process h4{margin-bottom:2rem;font-weight:300;color:white}.process h4 span{color:yellow}.process .incomming{width:100%}.process .incomming h4{color:white;font-weight:300}.process .incomming p{margin-top:0.5rem;background-color:#A9CCE3;padding:1.2rem;font-size:1rem;border-radius:5px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.process button{width:30%;border:none;background-color:#E74C3C;color:white;padding:10px}.process button:hover{cursor:pointer}.process .crypt{width:100%}.process .crypt h4{color:white;font-weight:300}.process .crypt p{margin-top:0.5rem;background-color:#A9CCE3;padding:1.2rem;font-size:1rem;border-radius:5px}

*{margin:0 auto;padding:0;box-sizing:border-box;color:black;font-family:"Muli", sans-serif}.App{width:100%;height:100vh;background-image:url(/static/media/moroccan-flower-dark.8fffdc8d.png);display:flex;justify-content:center;align-items:center}.App .right{flex:2 1}.App .left{flex:1 1}

*{margin:0 auto;padding:0;box-sizing:border-box;color:black;font-family:"Muli", sans-serif}.homepage{width:500px;height:500px;background-color:#ECF0F1;background-image:url(/static/media/double-bubble-outline.0694bcab.png);display:flex;flex-direction:column;padding:2rem;justify-content:space-evenly;border-radius:5px}.homepage input{height:50px;width:80%;text-decoration:none;background-color:#D4E6F1;border:none;padding-left:1rem;border-radius:5px}.homepage input:focus{outline:none}.homepage button{font-size:1rem;padding:0.5rem 1rem 0.5rem 1rem;margin:10px;width:100px;border:none;background-color:#ffac41;border-radius:5px;text-align:center;color:white}.homepage button:hover{cursor:pointer}

